.log-screen {
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: black; }
  .log-screen[data-state="log"] {
    color: white; }
  .log-screen[data-state="error"] {
    color: red; }
  .log-screen[data-state="warning"] {
    color: yellow; }
  .log-screen[data-state="success"] {
    color: green; }
  .log-screen[data-state="loading"] {
    --progress: 1;
    color: #FFFFFF; }
    .log-screen[data-state="loading"] .log-screen__content::before {
      content: '';
      width: 100%;
      height: 2px;
      background: rgba(255, 255, 255, 0.3);
