      -o-transition: transform 300ms cubic-bezier(0.25, 0.1, 0.25, 1);
      transition: transform 300ms cubic-bezier(0.25, 0.1, 0.25, 1);
      content: '';
      width: 100%;
      height: 2px;
      background: #FFFFFF;
      position: absolute;
      bottom: -1em;
      left: 50%;
      transform: translateX(-50%) scale(var(--progress, 0), 1); }
  .log-screen__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    text-transform: uppercase; }
  .log-screen__title {
